	async function searchCheck() {
		var ser = document.getElementById("ser").value
		var num = document.getElementById("num").value
		var body = await fetch(`https://api.allorigins.win/get?url=http%3A%2F%2F176.62.87.226%3A5400%2FFGW%2Fservice%2Fcheque%2F${ser}${num}`)
		body = await body.json()
		body = JSON.parse(body.contents)
		if(body.error == null) {
			console.log(body.data.url)
			var params = new URLSearchParams(body.data.url.split("?")[1])
			document.getElementById("fn").textContent = "ФН: " + params.get("fn")
			document.getElementById("fp").textContent = "ФП: " + params.get("fp")
			document.getElementById("fd").textContent = "ФД: " + params.get("i")

			document.getElementById("sum1").textContent = parseFloat(params.get("s")) / 25 + ".00x25.00"
			document.getElementById("sum2").textContent = "=" + params.get("s")
			document.getElementById("sum3").textContent = params.get("s")
			document.getElementById("sum4").textContent = "=" + params.get("s")
			document.getElementById("sum5").textContent = "=" + params.get("s")
			document.getElementById("qr").setAttribute("src", "https://proverkacheka.com/qrcode/generate?text=" + encodeURIComponent(body.data.url.split("?")[1]))

			document.getElementById("result").style.visibility = "visible"
		}
	}

